import React, { useState } from "react";
import { useForm } from "react-hook-form";
import firebase from "../firebase";
import { Button, Input } from "antd";
import {ModalPopup} from "./Admin/ModifyModal"

function Login() {
  const { register, errors, handleSubmit } = useForm({
    mode: "onChange",
  });
  const onSubmit = async (data) => {
    try {
      setLoading(true);
      await firebase
        .auth()
        .signInWithEmailAndPassword(data.email, data.password);
      setLoading(false);
    } catch (error) {
      setErrorFromSubmit(error.message);
      setLoading(false);
      setTimeout(() => {
        setErrorFromSubmit("");
      }, 5000);
    }
  };
  const [errorFromSubmit, setErrorFromSubmit] = useState("");
  const [loading, setLoading] = useState(false);

  const [InputEmail, setInputEmail] = useState(false);
  const [InputPw, setInputPw] = useState(false);
  const onInputEmail = (e) => {
    setInputEmail(e.target.value);
  };
  const onInputPw = (e) => {
    setInputPw(e.target.value);
  };

  const [PwChangeInput, setPwChangeInput] = useState('')
  const OnPwChangeInput = (e) => {
    setPwChangeInput(e.target.value)
  }
  const onPwChange = () => {
    const auth = firebase.auth();
    const emailAddress = PwChangeInput
    console.log(emailAddress)
    auth.sendPasswordResetEmail(emailAddress).then(function() {
      alert('    .')
      onPwModal();
    }).catch(function(error) {
      alert('    .')
    });    
  }

  const [PwModal, setPwModal] = useState(false)
  const onPwModal = () => {
    setPwModal(!PwModal)
  }

  return (
    <>
      <div className="join-form-wrap">
        <form className="join-form" onSubmit={handleSubmit(onSubmit)}>
          <div className="input-box">
            <input
              name="email"
              type="email"
              id="email"
              onChange={onInputEmail}
              ref={register({ required: true, pattern: /^\S+@\S+$/i })}
            />
            <label
              htmlFor="email"
              className={"place-holder " + (InputEmail && "on")}
            >
              <span></span>
            </label>          
          </div>
          <div className="input-box">
            <input
              type="password"
              onChange={onInputPw}
              name="password"
              id="password"
              ref={register({ required: true, minLength: 6 })}
            />
            <label
              htmlFor="password"
              className={"place-holder " + (InputPw && "on")}
            >
              <span></span>
            </label>
            {errors.password && errors.password.type === "required" && (
              <p>  </p>
            )}
            {errors.password && errors.password.type === "minLength" && (
              <p> 6  .</p>
            )}
            {errorFromSubmit && <p>{errorFromSubmit}</p>}
          </div>
          <input type="submit" value="" disabled={loading} />
        </form>
        <div style={{position:"relative",marginTop:"10px"}}>
          <a href="javascript:;" onClick={onPwModal}> </a>
          {
            PwModal &&
            <ModalPopup>
              <h3 style={{fontWeight:"bold",textAlign:"center"}}> </h3>
              <Input placeholder="   " type="text" value={PwChangeInput} onChange={OnPwChangeInput} />
              <div className="flex-box j-center" style={{marginTop:"10px"}}>
                <Button type="primary" style={{marginRight:"5px"}} onClick={onPwChange}> </Button>
                <Button onClick={onPwModal}></Button>
              </div>
            </ModalPopup>
          }
        </div>
      </div>
    </>
  );
}

export default Login;
