import React, { useState, useRef, useEffect } from 'react'
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { Form, Input, Button, Space, Radio, Checkbox, Upload, Switch, DatePicker, Select, Row, Col } from 'antd';
import { MinusCircleOutlined, PlusOutlined, UploadOutlined } from '@ant-design/icons';
import uuid from "react-uuid";
import firebase from "../../firebase";
import { getFormatDate } from "../CommonFunc";
import { OderModalPopup } from "../OrderModal";
import moment from 'moment';
const { RangePicker } = DatePicker;
function ResearchWrite() {
  const userInfo = useSelector((state) => state.user.currentUser);

  const [TotalUser, setTotalUser] = useState()
  const [TempList, setTempList] = useState()
  useEffect(() => {
    let userArr = [];
    firebase
    .database()
    .ref("users")
    .once("value", (snapshot) => {
      snapshot.forEach(el=>{
        userArr.push({
          uid:el.key,
          auth:el.val().auth ? el.val().auth : "",
          name:el.val().name,
          part:el.val().part,
          role:el.val().role
        })
      })
      userArr.sort((a,b)=>{
        return a.part < b.part ? -1 : 1 
      })
      console.log(userArr)
      setTotalUser(userArr);
    });

    let arr = [];
    firebase.database().ref(`research_temp/${userInfo.uid}`)
    .once("value")
    .then((snapshot) => {
      snapshot.forEach(el=>{
        arr.push(el.val())          
      })
      arr = arr.sort((a,b)=>{
        return b.timestamp - a.timestamp
      })
      setTempList(arr)
    })

    return () => {
      
    }
  }, [])

  const btnToList = useRef();

  const uid = uuid();

  const disabledDate = (current) => {
    return current && current < moment().subtract(1, 'days');
  }

  const [DateLimitState, setDateLimitState] = useState(false)
  const dateLimit = () => {
    setDateLimitState(!DateLimitState)
  }  

  const finishDataSave = (values) => {
    let uploadURL = [];            
    const getImgUrl = async () => {
    values.upload && values.upload.map(el=>{
    let getImg = async () => {
    let uploadTask = await firebase
        .storage()
        .ref("research")
        .child(`image/${uid}/${uuid()}`)          
        .put(el.originFileObj, el.type);
          uploadTask.ref.getDownloadURL()
          .then(url => {
            uploadURL.push({
              name:el.originFileObj.name,
              url:url
            });
            firebase.database().ref('research')
            .child(uid)
            .update({
              image: uploadURL
            });                       
          });
        }
        getImg();
      })
    }
    getImgUrl(); 
      firebase.database().ref('research')
      .child(uid)
      .update({
        title:values.title,
        type:values.type,
        option:values.option_list ? values.option_list : '',
        member:values.member ? values.member : false,
        member_check:values.member_check ? values.member_check : '',
        etc:values.etc ? values.etc : '',
        uid:uid,
        alba:values.alba ? values.alba : false,
        intern:values.intern ? values.intern : false,
        secret:values.secret ? values.secret : false,
        date:getFormatDate(new Date()).full_,
        timestamp:new Date().getTime(),
        limit_start:values.time_limit ? values.time_limit[0]._d.getTime() : 0,
        limit_end:values.time_limit ? values.time_limit[1]._d.getTime() : 99999999999999,
      });
    }      

 
  
  const onFinish = async (values) => {
    if(values.temp){
      let uid = uuid();
      firebase.database().ref(`research_temp/${userInfo.uid}`)
      .child(uid)
      .update({
        title:values.title,
        type:values.type,
        option:values.option_list ? values.option_list : '',
        member:values.member ? values.member : false,
        member_check:values.member_check ? values.member_check : '',
        etc:values.etc ? values.etc : '',
        uid:uid,
        alba:values.alba ? values.alba : false,
        intern:values.intern ? values.intern : false,
        secret:values.secret ? values.secret : false,
        date:getFormatDate(new Date()).full_,
        timestamp:new Date().getTime(),
        limit_start:values.time_limit ? values.time_limit[0]._d.getTime() : 0,
        limit_end:values.time_limit ? values.time_limit[1]._d.getTime() : 99999999999999,
      });
      btnToList.current && btnToList.current.click();
    }else{
      if(values.type != 2){
        let listLength = values.option_list.length;
        let count = 0;

        values.option_list.map(el=>{
          el.option_photo = el.option_photo ? el.option_photo : "";
          el.option_a = el.option_a ? el.option_a : "";
          el.option_type = el.option_type ? el.option_type : "";
          let urlArr = [];  
          count++;    
          const getOptionImgUrl = (photo) => {
              if(!photo){
                if(count == listLength){
                  finishDataSave(values);
                  btnToList.current && btnToList.current.click();
                }
              }else{
              photo.map((list,idx)=>{
              let getImg = async () => {
              let uploadTask = await firebase
                  .storage()
                  .ref("research")
                  .child(`image/${uid}/${uuid()}`)          
                  .put(list.originFileObj, list.type);
                    uploadTask.ref.getDownloadURL()
                    .then(url => {
                      urlArr.push({
                        uid:idx,
                        status:"done",
                        name:list.originFileObj.name,
                        url:url
                      });
                      firebase.database().ref('research')
                      .child(`${uid}/option/${idx}`)
                      .update({
                        option_photo: urlArr
                      });                
                      el.option_photo = urlArr ? urlArr : '';                
                      if(count == listLength){
                        finishDataSave(values);
                        btnToList.current && btnToList.current.click();
                      }
                    });
                  }
                  getImg();              
                })
              }          
            }
          getOptionImgUrl(el.option_photo);
          el.option_photo = el.option_photo ? el.option_photo : '';
        })
      }else{
        finishDataSave(values);
        btnToList.current && btnToList.current.click();
      }
    }
      
  };



  const [TypeState, setTypeState] = useState()
  const typeOptions = [
    {label: '', value: 1},
    {label: '', value: 2},
    {label: '', value: 3}
  ]
  const onChangeType = (e) => {
    setTypeState(e.target.value)
  }

  const normFile = (e) => {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  const onSelectChange = (e) => {
    console.log(e)
  }


  const [onMember, setonMember] = useState(false)
  const onMemberChange = (e) => {
    console.log(TotalUser)
    setonMember(!onMember)
  }


  const onTempSubmit = (e) => {
    e.preventDefault();
    console.log(document.forms.dynamic_form_nest_item)
  }

  const [TempState, setTempState] = useState(false)
  const onTempPop = () => {
    setTempState(!TempState)
  }


  return (
    <>     
      <Form name="dynamic_form_nest_item" className="research-form" onFinish={onFinish} autoComplete="off">
        <Form.Item
          name="title"
          rules={[{ required: true, message: '  .'}]}
        >
          <Input placeholder="" />
        </Form.Item> 
        <Form.Item 
          name="type"
          label=" "
          rules={[{ required: true, message: '  .'}]}
        >
          <Radio.Group
            options={typeOptions}
            onChange={onChangeType}
            value={TypeState}
            optionType="button"
          />
        </Form.Item>
        {TypeState && TypeState == 1 &&
          <Form.List name="option_list">
            {(fields, { add, remove }) => (
              <>
                {fields.map(({ key, name, fieldKey, ...restField }) => (
                  <Space key={key} style={{ display: 'flex', marginBottom: 5 }} align="baseline">
                    <Form.Item
                      {...restField}
                      name={[name, 'option']}
                      fieldKey={[fieldKey, 'option']}
                      rules={[{ required: true, message: '  .' }]}
                    >
                      <Input placeholder="" />
                    </Form.Item>                  
                    <MinusCircleOutlined onClick={() => remove(name)} />
                  </Space>
                ))}
                <Form.Item>
                  <Button type="dashed" onClick={() => add()} block icon={<PlusOutlined />}>
                    Add field
                  </Button>
                </Form.Item>
              </>
            )}
          </Form.List>
        }
        {TypeState && TypeState == 3 &&
          <Form.List name="option_list">
            {(fields, { add, remove }) => (
              <>
                {fields.map(({ key, name, fieldKey, ...restField }) => (
                  <Space key={key} style={{ display: 'flex', marginBottom: 5 }} align="baseline">
                    <Form.Item
                      {...restField}
                      name={[name, 'option_q']}
                      fieldKey={[fieldKey, 'option_q']}
                      rules={[{ required: true, message: '  .' }]}
                    >
                      <Input placeholder="" />
                    </Form.Item>     
                    <Form.Item 
                      label=""
                      {...restField}
                      name={[name, 'option_type']}
                      fieldKey={[fieldKey, 'option_type']}
                    >
                      <Select defaultValue="0" onChange={onSelectChange}>
                        <Select.Option value="0"></Select.Option>
                        <Select.Option value="1"></Select.Option>
                        <Select.Option value="2"></Select.Option>
                      </Select>
                    </Form.Item> 
                    <Form.Item
                      {...restField}
                      name={[name, 'option_a']}
                      fieldKey={[fieldKey, 'option_a']}
                    >
                      <Input placeholder="  ,   " />
                    </Form.Item> 
                    <Form.Item
                      {...restField}
                      name={[name, 'option_photo']}
                      label=" "
                      fieldKey={[fieldKey, 'option_photo']}
                      getValueFromEvent={normFile}                      
                    >
                      <Upload name="logo" listType="picture">
                        <Button icon={<UploadOutlined />}>Click to upload</Button>
                      </Upload>
                    </Form.Item>
                    <MinusCircleOutlined onClick={() => remove(name)} />
                  </Space>
                ))}
                <Form.Item>
                  <Button type="dashed" onClick={() => add()} block icon={<PlusOutlined />}>
                    Add field
                  </Button>
                </Form.Item>
              </>
            )}
          </Form.List>
        }        
        <Form.Item
          name="upload"
          label=" "
          valuePropName="fileList"
          getValueFromEvent={normFile}
        >
          <Upload name="logo" listType="picture">
            <Button icon={<UploadOutlined />}>Click to upload</Button>
          </Upload>
        </Form.Item>
        <div className="flex-box">
          <Form.Item
            name="alba"
            valuePropName="checked"
            style={{marginRight:"13px"}}
          >
            <Checkbox> </Checkbox>
          </Form.Item> 
          <Form.Item
            name="intern"
            valuePropName="checked"
            style={{marginRight:"13px"}}
          >
            <Checkbox> </Checkbox>
          </Form.Item> 
          <Form.Item
            name="secret"
            valuePropName="checked"            
            style={{marginRight:"13px"}}
          >
            <Checkbox> </Checkbox>
          </Form.Item>          
          <Form.Item
            name="member"
            valuePropName="checked"
            onChange={onMemberChange}
          >
            <Checkbox> </Checkbox>
          </Form.Item> 
        </div>
        {onMember && TotalUser &&
          <>
            <Form.Item name="member_check">
              <Checkbox.Group>
                <Row>                
                {TotalUser.map((el,idx)=>(
                  <Col span={4}>
                    <Checkbox key={idx} value={el.uid}>{el.name}({el.part})</Checkbox>
                  </Col>
                ))}
                </Row>
              </Checkbox.Group>
            </Form.Item>
          </>
        }
        <Form.Item
          name="etc"          
          rules={[{ required: true}]}
        >
          <Input.TextArea placeholder="" />
        </Form.Item> 
        <Form.Item
          label="" style={{marginBottom:"7px"}}
        >
          <Switch onChange={dateLimit} />
        </Form.Item>
        {DateLimitState &&
          <Form.Item
            name="time_limit"
          >
            <RangePicker  
              showTime 
              disabledDate={disabledDate} 
            />
          </Form.Item>
        }
        <Form.Item
          name="temp"
          valuePropName="checked"
        >
          <Checkbox></Checkbox>
        </Form.Item>

        <div className="flex-box j-center" style={{marginTop:"15px",position:"relative"}}>
          {TempList &&
            <>
              <Button onClick={onTempPop}>
                 ({TempList.length})            
              </Button>
              {TempState &&
                <OderModalPopup className="research-temp-list">
                  <ul>
                  {TempList.map((el,idx)=>(
                    <li>
                      <Link className="flex-box between" to={{
                        pathname: `/research_temp`,
                        state: {
                          uid:el.uid,
                          temp:true
                        }
                      }}>
                        <span className="subject">{el.title}</span>
                        <span className="date">{el.date}</span>
                      </Link>
                    </li>
                  ))}
                  </ul>
                </OderModalPopup>
              }
            </>
          }
          <Button type="primary" htmlType="submit" style={{width:"100px",marginLeft:"5px"}}>
            
          </Button>
          <Button style={{marginLeft:"5px"}}>
            <Link ref={btnToList} to="/research"></Link>
          </Button>
        </div>    
      </Form>
    </>
  )
}

export default ResearchWrite
